import React, { Component } from "react";
import axios from 'axios';
import { paginate } from './utils/paginate';
import Pagination from './common/pagination';
import "./App.css";

// API (EndPoint) url:
const apiEndpoint = 'https://jsonplaceholder.typicode.com/posts';

class App extends Component {
  state = {
    posts: [],
    pageSize: 6,
    currentPage:1
  };


  // Best Place to Call the Server and Get Data HERE:
  // Remember the 'async' keyword. IMPORTANT !!!
  async componentDidMount() {
    // axios.get(): used to Get Data and return a promise.
    // Promise = is an object that holds the results of an Asyncronous operation.
    // Status: pending > resolved (success) OR rejected (failure).
     // await => modern Javascript keyword.
    const { data: posts } = await axios.get(apiEndpoint);
    this.setState({ posts });
  }

 
  // Add Data to the Server:
  // Remember the 'async' keyword. IMPORTANT !!!
  handleAdd = async () => {
    
    // Data to Add:
    const obj = { title: 'Terminator', body: 'This is Terminator 2 movie...'};

    // Send the object 'obj' to the Server:
    // axios.post(): used to Create Data to the Server.
    // axios.post('api url', obj or data to send).
    const { data: post } = await axios.post(apiEndpoint,obj);
    
    // Add the data to our table in the Browser.
    const posts = [post, ...this.state.posts];
    this.setState({ posts });
  };



  handleUpdate = post => {
    console.log("Update", post);
  };

  handleDelete = post => {
    console.log("Delete", post);
  };

  // handlePageChange()
  handlePageChange = (page) => {
    this.setState({ currentPage: page});
  };


  getPagedData = () => {

    // Object Destructoring to cleaner our code:
     const { 
      pageSize, 
      currentPage
    } = this.state; 


    // Paginate Movies:
    const movies = paginate(this.state.posts,this.state.currentPage,this.state.pageSize);  
    console.log(movies);
    return { totalCount: 10, movies}
   
  };


  render() {

    const { pageSize, currentPage } = this.state;
    const { totalCount, movies} = this.getPagedData();
    console.log(totalCount,movies);


    return (
      <React.Fragment>
        <button className="btn btn-primary mb-2" onClick={this.handleAdd}>
          Add
        </button>
        <table className="table">
          <thead>
            <tr>
              <th>Title</th>
              <th>Update</th>
              <th>Delete</th>
            </tr>
          </thead>
          <tbody>
            {this.state.posts.map(post => (
              <tr key={post.id}>
                <td>{post.title}</td>
                <td>
                  <button
                    className="btn btn-info btn-sm"
                    onClick={() => this.handleUpdate(post)}
                  >
                    Update
                  </button>
                </td>
                <td>
                  <button
                    className="btn btn-danger btn-sm"
                    onClick={() => this.handleDelete(post)}
                  >
                    Delete
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <Pagination 
          itemsCount={totalCount} 
          pageSize={pageSize}
          currentPage={currentPage}
          onPageChange={this.handlePageChange}
        />
      </React.Fragment>
    );
  }
}

export default App;



