import _ from 'lodash';

// Function to paginate:
export function paginate(items,pageNumber,pageSize){
    // calculate the starting Index of the items of the pageNumber:
    const startIndex = (pageNumber - 1) * pageSize;
    
    // convert items array into a lodash wrapper
    return _(items) // return a lodash object
        .slice(startIndex) // start from this index
        .take(pageSize) // take items from this current page.
        .value(); // convert lodash wrapper into a regular array.
}


